export default {
  name: "thread_manager",
  props: {
    topic_data: {},
  },
  destroyed() {
    this.listener_loginout.off();
    this.listener_login.off();
  },
  created() {
    this.listener_loginout = this.$hub.on('event:loginout', (data) => {
      this.$nextTick(
          () => {
            this.$forceUpdate();
          }
      );
    });
    this.listener_login = this.$hub.on('event:login', (data) => {
      this.$nextTick(
          () => {
            this.$forceUpdate();
          }
      );
    });
    if (this.topic_data) {
      if (!this.topic_data.manager_set) {
        this.topic_data.manager_set = {}
      }
    }
  },
  methods: {
    set_to_idnextop: function (action) {
      var post_data = {
        'id': this.topic_data.id,
        'action': action
      };
      this.$axios.post('/app/topic/setindextop', post_data).then((response) => {
        var setindextop = response.data.setindextop;
        this.topic_data.manager_set.setindextop = setindextop;
        this.$forceUpdate();
      })
    },
    set_to_carousel: function (action) {
      var post_data = {
        'id': this.topic_data.id,
        'action': action
      };
      this.$axios.post('/app/topic/setcarousel', post_data).then((response) => {
        console.log('setcarousel response:', response);
        var setcarousel = response.data.setcarousel;
        this.topic_data.manager_set.setcarousel = setcarousel;
        this.$forceUpdate();
      })
    },
    set_to_essence: function (action) {

      var post_data = {
        'id': this.topic_data.id,
        'action': action
      };
      this.$axios.post('/app/topic/setessence', post_data).then((response) => {
        console.log('settoessence response:', response);
        var settoessence = response.data.settoessence;
        this.topic_data.manager_set.settoessence = settoessence;
        this.$forceUpdate();
      })
    },
    set_to_top: function (action) {

      var post_data = {
        'id': this.topic_data.id,
        'action': action
      };
      this.$axios.post('/app/topic/settotop', post_data).then((response) => {
        console.log('settotop response:', response);
        var settotop = response.data.settotop;
        this.topic_data.manager_set.settotop = settotop;
        this.$forceUpdate();
      })
    }
  }
}
